"use client";

import React, { useState } from "react";

interface Player {
  id: string;
  name: string;
  score: number;
}

const samplePlayers: Player[] = [
  { id: "1", name: "Alice", score: 10 },
  { id: "2", name: "Bob", score: 15 },
];

export default function Games() {
  const [players, setPlayers] = useState<Player[]>(samplePlayers);
  const [currentPlayer, setCurrentPlayer] = useState<Player | null>(null);
  const [gameMessage, setGameMessage] = useState<string>("");

  const startChallenge = (player: Player) => {
    setCurrentPlayer(player);
    setGameMessage(`Challenge started with ${player.name}!`);
  };

  const endChallenge = () => {
    setCurrentPlayer(null);
    setGameMessage("Challenge ended.");
  };

  return (
    <div className="p-6 bg-white rounded-lg shadow">
      <h2 className="text-2xl font-bold mb-4">Educational Games</h2>
      <p className="mb-4">{gameMessage}</p>
      {!currentPlayer ? (
        <div>
          <h3 className="text-xl font-semibold mb-2">Challenge a Classmate</h3>
          <ul className="space-y-2">
            {players.map((player) => (
              <li key={player.id} className="flex justify-between items-center border p-3 rounded hover:shadow-md transition">
                <span>{player.name}</span>
                <button
                  onClick={() => startChallenge(player)}
                  className="bg-black text-white px-3 py-1 rounded hover:bg-gray-900 transition"
                >
                  Challenge
                </button>
              </li>
            ))}
          </ul>
        </div>
      ) : (
        <div>
          <button
            onClick={endChallenge}
            className="bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700 transition"
          >
            End Challenge
          </button>
        </div>
      )}
    </div>
  );
}
